import {
  Box,
  Image,
  Flex,
  Heading,
  Spacer,
  Button,
} from "@chakra-ui/react";
import resume from "../Download/resume.pdf"

const Introduction = () => {
  return (
    <Box w="80%" m="auto" display="grid" gridTemplateColumns={{sm:"repeat(1, 1fr)", md:"repeat(2, 1fr)", lg:"repeat(2,1fr)"}} gap="8" pt="7%" id="about" >
      <Box >
        <Box borderRadius="50%" overflow="hidden" >
          <Image
            display="block" m="auto"
            src="https://ca.slack-edge.com/T049YSPNG02-U04AV5TJU59-f322e8cb93ac-512"
            alt="Omchand.jpg"
          />
        </Box>
      </Box>
      <Box textAlign="left" >
        <Heading mt="15%" as="h1" size="2xl">
          Hello.
        </Heading>
        <Heading mt="5%" size="md">
          I'm Omchand Kanu.
        </Heading>
        <Heading mt="2%" size="md">Full Stack Web Developer, Enthusiast to Learn New FrameWorks</Heading>
        <Flex h="7vh" mt="7%" w={{sm:"80%", md:"60%", lg:"60%"}} border="1px solid red" >
          <Spacer />
          <a target="_blank" href="https://github.com/Omchand27">
          <Image h="7vh" transition="1"
            _hover={{boxShadow: "black 0px 5px 15px", transform:"scale(1.2)"}} borderRadius="50%"
            src="data:image/png;base64,"
          /></a>
          <Spacer />
          <a target="_blank" href="https://www.linkedin.com/in/omchand-kanu-542a3423a/">
          <Image h="7vh" transition="1"
            _hover={{boxShadow: "blue 0px 5px 15px", transform:"scale(1.2)"}} borderRadius="50%"
            src="https://upload.wikimedia.org/wikipedia/commons/thumb/f/f8/LinkedIn_icon_circle.svg/2048px-LinkedIn_icon_circle.svg.png"
          /></a>
          <Spacer />
          <a href=""></a>
          <Image h="7vh" transition="1"
            _hover={{boxShadow: "blue 0px 5px 15px", transform:"scale(1.2)"}} borderRadius="50%"
            src="https://www.freeiconspng.com/thumbs/logo-twitter-png/cricle-twitter-emblem-png-clipart-8.png"
          />
          <Spacer />
          <a href=""></a>
          <Image h="7vh" transition="1"
            _hover={{boxShadow: "red 0px 5px 15px", transform:"scale(1.2)"}} borderRadius="50%"
            src="https://www.shareicon.net/download/2015/09/26/107663_media_512x512.png"
          />
          <Spacer />
        </Flex>
        <a href={resume} download="Omchand_Kanu_Resume">
        <Button
          mt="7%" mr="3%"
          bg="#fc1056"
          color="white"
          p="5% 7% 5% 7%"
          fontSize="2xl"
          _hover={{
            bg: "#fc1056",
            boxShadow: "rgba(0, 0, 0, 0.35) 0px 5px 16px",
          }} 
        >
          Download CV
        </Button>
          </a>
        <Button
          mt="7%"
          bg="#fc1056"
          color="white"
          p="5% 10% 5% 10%"
          fontSize="2xl"
          _hover={{
            bg: "#fc1056",
            boxShadow: "rgba(0, 0, 0, 0.35) 0px 5px 16px",
          }}
        >
          <Box>
            Contact</Box>
        </Button>
      </Box>
    </Box>
  );
};

export default Introduction;
